// ** React Imports
import { Fragment } from 'react'

import { Link } from 'react-router-dom'

// ** Custom Components
import Avatar from '@components/avatar'

// ** Store & Actions
import { deleteInvoice } from '../store/actions'
import { store } from '@store/storeConfig/store'

// ** Third Party Components
import {
    Badge,
    UncontrolledDropdown,
    DropdownMenu,
    DropdownToggle,
    DropdownItem,
    UncontrolledTooltip
} from 'reactstrap'
import {
    Eye,
    TrendingUp,
    Send,
    MoreVertical,
    Download,
    Edit,
    Trash,
    Copy,
    CheckCircle,
    Save,
    ArrowDownCircle,
    Info,
    PieChart
} from 'react-feather'

// ** Vars
const invoiceStatusObj = {
    Sent: { color: 'light-secondary', icon: Send },
    Paid: { color: 'light-success', icon: CheckCircle },
    Draft: { color: 'light-primary', icon: Save },
    Downloaded: { color: 'light-info', icon: ArrowDownCircle },
    'Past Due': { color: 'light-danger', icon: Info },
    'Partial Payment': { color: 'light-warning', icon: PieChart }
}

// ** renders client column
const renderClient = row => {
    const stateNum = Math.floor(Math.random() * 6),
        states = ['light-success', 'light-danger', 'light-warning', 'light-info', 'light-primary', 'light-secondary'],
        color = states[stateNum]

    if (row.avatar.length) {
        return <Avatar className='mr-50' img={row.avatar} width='32' height='32' />
    } else {
        return <Avatar color={color} className='mr-50' content={row.client ? row.client.name : 'John Doe'} initials />
    }
}

// ** Table columns
export const columns = [
    {
        name: 'Title',
        selector: 'total',
        sortable: true,
        minWidth: '350px',
        cell: row => {
            return (<Link to={`/apps/task/posted-detail/${row.id}`}>
                <span style={{ fontSize: 14 }}><b>Hire 5 new Fresher or Experienced,... </b></span>
            </Link>)
        }
    },
    {
        name: 'Description',
        selector: 'total',
        sortable: true,
        minWidth: '400px',
        cell: row => <span>Chocolate cake topping bonbon jujubes donut sweet wafer</span>
    },
    {
        name: 'Price',
        selector: 'total',
        sortable: true,
        minWidth: '10 0px',
        cell: row => <span>${row.total || 0}</span>
    },
    {
        name: 'Issued Date',
        selector: 'dueDate',
        sortable: true,
        minWidth: '150px',
        cell: row => row.dueDate
    },
    {
        name: 'Attended Number',
        selector: 'dueDate',
        sortable: true,
        minWidth: '50px',
        // cell: row => 0
        cell: row => <span>{row.num || 0}</span>

    }

]
